import React,{Component} from 'react'

import copyState from './copyState'
import './shoppingList.css'

class shoppingList extends Component{
    constructor(props){
        super()
        this.state={
            statusColumn:0,
            selectAll:false
        }
    }
    render(){
        let priceSum=0,
            float='00',
            goodsAmount=0,
            total=0,
            totalFloat='00'
        // let image={}
        // for (let i in this.props.shoplist){
        //     image=<li>
        //     <div className="shoplist-goods-info">
        //         <img src={require("./images/shopListSample1.png")} alt=""/>
        //         <div>
        //             <b>维萨德</b>
        //             <p>小圆桌/深灰色</p>
        //         </div>
        //     </div>
        //     <ul className="shoplist-goods-price">
        //         <li>￥300.00</li>
        //         <li className="shoplist-goods-amount">
        //             <div>
        //                 <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
        //                     <path fill="#cc8c31" d="M92 484c-15.5 0-28 12.5-28 28s12.5 28 28 28l840 0c15.5 0 28-12.5 28-28s-12.5-28-28-28L92 484z" />
        //                 </svg>
        //             </div>
        //             <input type="text" value={1}/>
        //             <div>
        //                 <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
        //                     <path fill="#cc8c31" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
        //                 </svg>
        //             </div>
        //         </li>
        //         <li>￥300.00</li>
        //         <li><a href="">删除</a></li>
        //     </ul>
        // </li>
        return(
            <div id="shoplist">
                <div className="container">
                    <div className="shoplist-banner">
                        <div><img src={require("./images/logo_shoppingList.png")} alt=""/></div>
                        <div className="shoplist-search-bar">
                            <form action="" method="get">
                                <input type="text"/>
                                <a>
                                    <svg className="icon" width="44px" height="44.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                        <path fill="#333333" d="M621.664 653.664c-44.48 31.68-98.88 50.336-157.664 50.336-150.208 0-272-121.792-272-272s121.792-272 272-272c150.208 0 272 121.792 272 272 0 75.104-30.432 143.104-79.68 192.32l191.904 191.904c8.8 8.8 8.576 22.912-0.256 31.744-8.896 8.896-23.04 8.96-31.744 0.256l-194.592-194.592zM464 672c132.544 0 240-107.456 240-240s-107.456-240-240-240c-132.544 0-240 107.456-240 240s107.456 240 240 240l0 0z" />
                                    </svg>
                                </a>
                                <button type="submit">搜索</button>
                            </form>
                        </div>
                    </div>
                    <div>
                        <div className="shoplist-status">
                            <ul>
                                <li className={this.state.statusColumn===0?"shoplist-selected":null} onClick={this.lookForShoplistStatus.bind(this,0)}><a href="javascript:void(0)">全部商品</a></li>
                                <li className={this.state.statusColumn===1?"shoplist-selected":null} onClick={this.lookForShoplistStatus.bind(this,1)}><a href="javascript:void(0)">降价商品</a></li>
                                <li className={this.state.statusColumn===2?"shoplist-selected":null} onClick={this.lookForShoplistStatus.bind(this,2)}><a href="javsscript:void(0)">库存紧张</a></li>
                            </ul>
                            <a href="javascript:void(0)">结算</a>
                            <div>
                                <span>已选商品（不含运费）</span>
                                <span>{priceSum}.{float}</span>
                            </div>
                        </div>
                        <div className="shoplist-sort">
                            <div>
                                {this.state.selectAll?
                                <input type="checkbox" onChange={this.ifselected.bind(this)} selected/>:
                                <input type="checkbox" onChange={this.ifselected.bind(this)}/>}
                                <span>全选</span>
                                <span>商品详情</span>
                            </div>
                            <ul>
                                <li>单价</li>
                                <li>数量</li>
                                <li>全部</li>
                                <li>操作</li>
                            </ul>
                        </div>
                        <ul className="shoplist-goods">
                            <li>
                                <div className="shoplist-goods-info">
                                    <img src={require("./images/shopListSample1.png")} alt=""/>
                                    <div>
                                        <b>维萨德</b>
                                        <p>小圆桌/深灰色</p>
                                    </div>
                                </div>
                                <ul className="shoplist-goods-price">
                                    <li>￥300.00</li>
                                    <li className="shoplist-goods-amount">
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                <path fill="#cc8c31" d="M92 484c-15.5 0-28 12.5-28 28s12.5 28 28 28l840 0c15.5 0 28-12.5 28-28s-12.5-28-28-28L92 484z" />
                                            </svg>
                                        </div>
                                        <input type="text" value={1}/>
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                <path fill="#cc8c31" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
                                            </svg>
                                        </div>
                                    </li>
                                    <li>￥300.00</li>
                                    <li><a href="">删除</a></li>
                                </ul>
                            </li>
                            <li>
                                <div className="shoplist-goods-info">
                                    <img src={require("./images/shopListSample2.png")} alt=""/>
                                    <div>
                                        <b>伊波利</b>
                                        <p>三人沙发床/欧丽塔/深灰色</p>
                                    </div>
                                </div>
                                <ul className="shoplist-goods-price">
                                    <li>￥3,900.00</li>
                                    <li className="shoplist-goods-amount">
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                    <path fill="#cc8c31" d="M92 484c-15.5 0-28 12.5-28 28s12.5 28 28 28l840 0c15.5 0 28-12.5 28-28s-12.5-28-28-28L92 484z" />
                                            </svg>
                                        </div>
                                        <input type="text" value={1}/>
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                <path fill="#cc8c31" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
                                            </svg>
                                        </div>
                                    </li>
                                    <li>￥3,900.00</li>
                                    <li><a href="">删除</a></li>
                                </ul>
                            </li>
                            <li>
                                <div className="shoplist-goods-info">
                                    <img src={require("./images/shopListSample3.png")} alt=""/><span></span>
                                    <div>
                                        <b>瑞贝克</b>
                                        <p>桌子/白色 橙色</p>
                                    </div>
                                </div>
                                <ul className="shoplist-goods-price">
                                    <li>￥300.00</li>
                                    <li className="shoplist-goods-amount">
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                <path fill="#cc8c31" d="M92 484c-15.5 0-28 12.5-28 28s12.5 28 28 28l840 0c15.5 0 28-12.5 28-28s-12.5-28-28-28L92 484z" />
                                            </svg>
                                        </div>
                                        <input type="text" value={1}/>
                                        <div>
                                            <svg className="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                                <path fill="#cc8c31" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
                                            </svg>
                                        </div>
                                    </li>
                                    <li>￥300.00</li>
                                    <li><a href="#">删除</a></li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="total-bg">
                    <div className="container total">
                        {this.state.selectAll?
                        <input type="checkbox" onChange={this.ifselected.bind(this)} selected/>:
                        <input type="checkbox" onChange={this.ifselected.bind(this)}/>}
                        <div className="total-price">
                            <span>全选</span>
                            <a href="javascript:void(0)">删除</a>
                            <a href="javascript:void(0)">分享</a>
                        </div>
                        <div className="total-price-sum">
                            <span>已选商品：<span>{goodsAmount}</span> 件</span>
                            <span>合计（不含运费）：￥<span>{total}.{totalFloat}</span></span>
                            <a href="javascript:void(0)">结算</a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    lookForShoplistStatus(val){
        let copystate=copyState(this.state)
        copystate.statusColumn=val
        this.setState(copystate)
    }
    ifselected(){
        let copystate=copyState(this.state)
        this.state.selectAll?copystate.selectAll=false:copystate.selectAll=true 
        this.setState(copystate)
    }
}

export default shoppingList


